
import React, {useState, useEffect} from "react";
import Axios from "axios"
import './App.css';
import Coin from "./Coin";

function App() {
const [coins, setCoins] = useState([])

const [search, setSearch]=useState("")

  useEffect(()=>{
   const fetchData = async()=>{
     const result = await Axios('https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=20&page=1&sparkline=false')
 console.log(result.data)
 setCoins(result.data)
   }
   fetchData()
 
  },[])

  const handleChange =e=>{
    setSearch(e.target.value)
  }

  const filteredCoins=coins.filter(coin=>
    coin.name.toLowerCase().includes(search.toLowerCase())
  )
  
  return (
    <div className="coin-app">
    <div className="coin-search">
      <h1 className="coin-text">
        Search a currency
      </h1>
      <form>
        <input type="text"  className="coin-input" placeholder="Search" onChange={handleChange} />
      </form>

    </div>
    {filteredCoins.map((coin)=>(
       <Coin key={coin.id} name={coin.name} image={coin.image} symbol={coin.symbol} volume={coin.total_volume} price={coin.current_price} priceChange={coin.price_change_percentage_24h} marketcap={coin.market_cap}/>
    ))}

    </div>
  );
}

export default App;
